
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0 10px
}
@media(min-width: 30em) {
  body {
    padding: 0 10vw
  }
}
canvas {
  max-height: 100vh;
  max-width: 100vh;
}
table {
  display: block;
  max-height: 80vh;
  max-width: 100vw;
  overflow: auto;
  font-size: .75em;
}
tr {
}
tr:nth-child(2n) {
  background-color: #eee
}
th, td {
  padding: 10px;
  white-space: nowrap;
  text-align: center;
}

/* Source: http://codepen.io/RRoberts/pen/pEXWEp License: MIT */
.loader {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  position: relative;
  margin: 0 auto;
}
.loader:before, .loader:after{
  content: "";
  position: absolute;
  top: 10px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  border: 10px solid transparent;
  border-top-color: #3498db;
}
.loader:before{
  z-index: 100;
  -webkit-animation: spin 1s infinite;
          animation: spin 1s infinite;
}
.loader:after{
  border: 10px solid #ccc;
}
@-webkit-keyframes spin{
0%{
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes spin{
0%{
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}



